    cargarCatalogoColeccion(catalogo: string) { return this.catalogoGeneralService.cargarCatalogoColeccion(catalogo); }


  cargarSetReferencias() {
    if (!this.setDeCamposService.setReferenciasSeleccionado) {
      return;
    }
        const { 
          nombreCompleto,
          relacion,
          tiempoDeConocerle,
          telefonoContacto,
          mailContacto,
        } = this.setDeCamposService.setReferenciasSeleccionado;; 
  

  
        this.setReferenciasForm.patchValue({ 
          nombreCompleto,
          relacion,
          tiempoDeConocerle,
          telefonoContacto,
          mailContacto,
        });
  }

