  isoDate(fecha:Date) { 
    var d=new Date(fecha);
      return d.getUTCFullYear() + '-' + this.pad(d.getUTCMonth() + 1) + 
      '-' + this.pad(d.getUTCDate()) + 'T' + this.pad(d.getHours()) + ':' 
      + this.pad(d.getUTCMinutes()) + ':' + this.pad(d.getUTCSeconds()); 
    }
  
    pad(n:number){return n<10 ? '0'+n : n}


//modal

openModalNombreCompleto(accion:string) {
  if (accion!='editar'){
    this.setDeCamposService.setDatosPersonalesNombreCompletoSeleccionado=undefined;
  }
  this.modalService
    .open(SetDatosPersonalesNombreCompletoComponent, {    backdropClass: "light-blue-backdrop",
    backdrop: false,
    animation: true,
    size: "lg", })
    .result.then((result) => {this.anadirNombreCompleto(result);});
}

//Campo - NombreCompleto

get nombreCompleto(): FormArray {
  return this.setReferenciasForm.get("nombreCompleto") as FormArray;
}

anadirNombreCompleto(campo: AbstractControl) {
  if (this.setDeCamposService.setDatosPersonalesNombreCompletoSeleccionado){
    this.borrarNombreCompleto(this.nombreCompletoIdEdicion);
  }
  this.nombreCompleto.push(campo);
}

editarNombreCompleto(campo:SetDatosPersonalesNombreCompleto,index:number) {
  this.nombreCompletoIdEdicion=index;
  if (campo){
    this.setDeCamposService.setDatosPersonalesNombreCompletoSeleccionado=campo;
    this.openModalNombreCompleto('editar');
  }
}

borrarNombreCompleto(indice: number) {
  this.nombreCompleto.removeAt(indice);
}

setCamposNombreCompleto(valores: JSON) {
  return setDeCampos(this.nombreCompletoConcatenacion,valores);
}






